import type { Metadata } from "next";
import { Playfair_Display, Inter } from "next/font/google";
import "./globals.css";
import { ThemeProvider } from "@/components/ThemeProvider";
import Script from "next/script";

const playfair = Playfair_Display({
  weight: ["700"],
  subsets: ["latin"],
  variable: "--font-playfair",
  display: "swap",
});

const inter = Inter({
  weight: ["400", "500", "700", "900"],
  subsets: ["latin"],
  variable: "--font-inter",
  display: "swap",
});

export const metadata: Metadata = {
  title: "Bindu Designs - Premium Interior Design",
  description: "Premium Interior Design for Homes & Offices. Transforming spaces with elegance.",
};

export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  return (
    <html lang="en" suppressHydrationWarning>
      <head>
        <link
          href="https://fonts.googleapis.com/css2?family=Material+Symbols+Outlined"
          rel="stylesheet"
        />
        {/* Inline theme initialization script to prevent FOUC */}
        <Script id="theme-init" strategy="beforeInteractive">
          {`
            (function() {
              function getSystemTheme() {
                if (typeof window === 'undefined') return 'light';
                const isDark = window.matchMedia('(prefers-color-scheme: dark)').matches;
                return isDark ? 'dark' : 'light';
              }

              function getSavedTheme() {
                try {
                  const saved = localStorage.getItem('theme');
                  if (saved === 'light' || saved === 'dark' || saved === 'system') {
                    return saved;
                  }
                } catch (e) {}
                return null;
              }

              function applyTheme(theme) {
                const root = document.documentElement;
                root.setAttribute('data-theme', theme);
                if (theme === 'dark') {
                  root.classList.add('dark');
                } else {
                  root.classList.remove('dark');
                }
              }

              const saved = getSavedTheme();
              const theme = saved || 'system';
              const resolved = theme === 'system' ? getSystemTheme() : theme;
              applyTheme(resolved);
            })();
          `}
        </Script>
      </head>
      <body
        className={`${playfair.variable} ${inter.variable} bg-background-light dark:bg-background-dark font-(family-name:--font-inter) text-text-light dark:text-text-dark antialiased`}
      >
        <ThemeProvider>
          {children}
        </ThemeProvider>
      </body>
    </html>
  );
}
